@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.handwriting {
    font-family: 'Great Vibes', cursive;
}

.mt-10vh {
  margin-top: 10vh;
}

.home {
    margin-top: 7.5vh;
    text-align: center;
}
  
.logo-container {
    width: 100%;
    height: 45vmin;

    background-image: url(/victoria-lodz-frontend/static/media/home-bg.b63dfc45.jpg);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: top center;
}
  
.app-logo {
    margin-top: 50px;
    height: 60%;
    pointer-events: none;
    border-radius: 25px;
}
  
@media (prefers-reduced-motion: no-preference) {
    .app-logo {
      -webkit-animation: spin-y infinite 5s linear;
              animation: spin-y infinite 5s linear;
    }
}
  
.home-container {
    font-size: calc(10px + 2vmin);
}
  
.home-container p {
    padding: 0 10vw;
    font-style: italic;
    font-weight: 300;
    font-size: calc(10px + 1vmin);
}
  
@-webkit-keyframes spin-y {
    from {
      transform: rotateY(0deg);
    }
    to {
      transform: rotateY(360deg);
    }
}
  
@keyframes spin-y {
    from {
      transform: rotateY(0deg);
    }
    to {
      transform: rotateY(360deg);
    }
}
  

.no-data-row {
    text-align: center;
    font-style: italic;
}

.team-highlight {
    background-color: rgba(68, 183, 52, 0.18) !important;
}

.footer {
    width: 100%;
    height: 80px;
    position: absolute;
    bottom: 0;
    color: grey;
}
